import { applyMiddleware, compose, createStore} from 'redux';

import apiMiddleware from 'store/api-middleware';
import handbookReducer from 'store/reducer';

export function configureStore(api = {}, middleware = [], preloadedState = {}) {
	const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
	const combinedMiddleware = [apiMiddleware(api), ...middleware];

	return createStore(
		handbookReducer,
		preloadedState,
		composeEnhancers(
			applyMiddleware(...combinedMiddleware)
		)
	);
}
