import { camelCased } from 'utils/strings';
import { deverror, devlog } from 'utils/devlog';

function isItemNode(tagName) {
	return ['categoryitem', 'documentitem', 'fileitem', 'processitem'].includes(tagName);
}

function CompendiaItemBase(itemNode, path = [], company, language) {
	this.access = true;
	this.children = [];
	this.company = company;
	this.id = this.getId(itemNode);
	this.language = language;
	this.links = [];
	this.path = [...path, this.id];
	this.processNode(itemNode);
}

CompendiaItemBase.prototype.processNode = function processNode(itemNode) {
	this.forEachChild(itemNode, (tagName, child) => {
		if (isItemNode(tagName.toLowerCase())) {
			const item = this.handleItemNode(child);
			this.children.push(item);
		} else {
			this.handleCommonNode(child);
		}
	});
};

CompendiaItemBase.prototype.forEachChild = function forEachChild(node, callback) {
	for (let i = 0; i < node.childNodes.length; i++) {
		const child = node.childNodes[i];
		if (child.nodeType === Node.ELEMENT_NODE) {
			const tagName = camelCased(child.tagName);

			callback(tagName, child);
		}
	}
};

CompendiaItemBase.prototype.handleAccessNode = function handleAccessNode(accessNode) {
	this.forEachChild(accessNode, (tagName, child) => {
		if (tagName.toLowerCase() === 'groups') {
			this.handleAccessGroupsNode(child);
		}
	});
};

CompendiaItemBase.prototype.getAccessFromAccessNode = function getAccessFromAccessNode(accessNode) {
	for (let i = 0; i < accessNode.childNodes.length; i++) {
		const child = accessNode.childNodes[i];
		if (child.nodeType === Node.ELEMENT_NODE) {
			let { tagName } = child;

			if (tagName.toLowerCase() === 'groups') {
				return this.getAccessFromGroupsNode(child);
			}
		}
	}

	return true;
};

CompendiaItemBase.prototype.getAccessFromGroupsNode = function getAccessFromGroupsNode(groupsNode) {
	const access = [];

	for (let i = 0; i < groupsNode.childNodes.length; i++) {
		const group = groupsNode.childNodes[i];
		if (group.nodeType === Node.ELEMENT_NODE) {
			let { tagName } = group;

			if (tagName.toLowerCase() === 'group') {
				let company = this.getAccessFromGroupNode(group);

				if (company) {
					access.push(company);
				}
			}
		}
	}

	return access.length === 0 ? true : access;
};

CompendiaItemBase.prototype.getAccessFromGroupNode = function getAccessFromGroupNode(groupNode) {
	for (let i = 0; i < groupNode.childNodes.length; i++) {
		const child = groupNode.childNodes[i];
		if (child.nodeType === Node.ELEMENT_NODE) {
			let { tagName } = child;

			if (tagName.toLowerCase() === 'title') {
				return child.textContent.replace('Ø', 'O').replace('SJB', 'SAB');
			}
		}
	}

	return false;
};

CompendiaItemBase.prototype.getId = function getId(itemNode) {
	for (let i = 0; i < itemNode.childNodes.length; i++) {
		const child = itemNode.childNodes[i];
		if (child.nodeType === Node.ELEMENT_NODE) {
			if (child.tagName.toLowerCase() === 'id') {
				return Number(child.textContent);
			}
		}
	}

	return null;
};

CompendiaItemBase.prototype.getDocumentIdFromProcessStepItem = function getDocumentIdFromProcessStepItem(processStepItem) {
	for (let i = 0; i < processStepItem.childNodes.length; i++) {
		const node = processStepItem.childNodes[i];
		if (node.tagName.toLowerCase() === 'documentitem') {
			for (let i = 0; i < node.childNodes.length; i++) {
				const node2 = node.childNodes[i];
				if (node2.tagName.toLowerCase() === 'id') {
					return Number(node2.textContent);
				}
			}
		}
	}

	return null;
};

CompendiaItemBase.prototype.getMenuItem = function getMenuItem() {
	return {
		access: this.access,
		id: this.id,
		title: this.title,
		type: this.type,
	};
};

CompendiaItemBase.prototype.handleCommonNode = function handleCommonNode(node) {
	const tagName = camelCased(node.tagName);
	const contextLabel = this.id ? `${this.id}:` : '';

	switch (tagName.toLowerCase()) {
	case 'id':
	case 'modified':
	case 'comeditorplacement':
		this[tagName] = Number(node.textContent);
		break;
	case 'bodycompendiafirst':
	case 'published':
		this[tagName] = Number(node.textContent) === 1;
		break;
	case 'title':
	case 'bodycompendia':
	case 'bodylocal':
	case 'url':
		this[tagName] = node.textContent;
		break;
	case 'access':
		this.access = this.getAccessFromAccessNode(node);
		break;
	case 'link':
	case 'linkitem':
		this.handleLinkNode(node);
		break;
	default:
		devlog(contextLabel, `Unhandled '${tagName}' nodes:`, node);
	}
};

CompendiaItemBase.prototype.handleItemNode = function handleItemNode(node) {
	let { tagName } = node;

	switch (tagName.toLowerCase()) {
	case 'categoryitem':
		return new CategoryItem(node, this.path, this.company, this.language);
	case 'documentitem':
		return new DocumentItem(node, this.path, this.company, this.language);
	case 'fileitem':
		return new FileItem(node, this.path, this.company, this.language);
	case 'processitem':
		return new ProcessItem(node, this.path, this.company, this.language);
	default:
		return false;
	}
};
	
CompendiaItemBase.prototype.handleLinkNode = function handleLinkNode(linkNode) {
	const link = {};

	try {
		if (linkNode.attributes.type) {
			link.type = linkNode.attributes.type.textContent;
		}
	} catch (error) {
		deverror(error);
	}

	for (let i = 0; i < linkNode.childNodes.length; i++) {
		const node = linkNode.childNodes[i];
		if (node.nodeType === Node.ELEMENT_NODE) {
			const { tagName } = node;

			if (isItemNode(tagName.toLowerCase())) {
				link.item = this.handleItemNode(node);
				if (link.item.type === FileItem.type) {
					link.href = link.item.getUrl();
				}
			} else {
				link[tagName] = node.textContent;
			}
		}
	}

	this.links.push(link);
};

export function CategoryItem(itemNode, path = [], company, language) {
	CompendiaItemBase.call(this, itemNode, path, company, language);
	this.type = CategoryItem.type;
}

CategoryItem.prototype = Object.create(CompendiaItemBase.prototype);
CategoryItem.prototype.getMenuItem = function getMenuItem() {
	const item = CompendiaItemBase.prototype.getMenuItem.call(this);
	item.childNodes = this.children.map(item => item.getMenuItem());

	return item;
};

Object.defineProperty(CategoryItem.prototype, 'constructor', {
	enumerable: false,
	value: CategoryItem,
	writable: true
});

export function DocumentItem(itemNode, path = [], company, language) {
	CompendiaItemBase.call(this, itemNode, path, company, language);
	this.type = DocumentItem.type;
}

DocumentItem.prototype = Object.create(CompendiaItemBase.prototype);

Object.defineProperty(DocumentItem.prototype, 'constructor', {
	enumerable: false,
	value: DocumentItem,
	writable: true
});

export function FileItem(itemNode, path = [], company, language) {
	CompendiaItemBase.call(this, itemNode, path, company, language);
	this.type = FileItem.type;
}

FileItem.prototype = Object.create(CompendiaItemBase.prototype);
FileItem.prototype.getMenuItem = function getMenuItem() {
	const item = CompendiaItemBase.prototype.getMenuItem.call(this);
	item.url = this.getUrl();

	return item;
};
FileItem.prototype.getUrl = function getUrl() {
	return `/api/compendia/v2.0/${this.company}/${this.language}/download/${this.id}`;
};

Object.defineProperty(FileItem.prototype, 'constructor', {
	enumerable: false,
	value: FileItem,
	writable: true
});

export function HandbookItem(itemNode, path = [], company, language) {
	CompendiaItemBase.call(this, itemNode, path, company, language);
	this.type = HandbookItem.type;
	this.documents = {};
	this.getDocuments(this);
}

HandbookItem.prototype = Object.create(CompendiaItemBase.prototype);
HandbookItem.prototype.getDocuments = function getDocuments(item) {
	if (item.children.length === 0) return;

	for (let child of item.children) {
		if (child instanceof DocumentItem || child instanceof ProcessItem || child instanceof FileItem) {
			this.documents[child.id] = child;
		}

		this.getDocuments(child);
	}
};
HandbookItem.prototype.getMenuItem = function getMenuItem() {
	return this.children.map(item => item.getMenuItem());
};

Object.defineProperty(HandbookItem.prototype, 'constructor', {
	enumerable: false,
	value: HandbookItem,
	writable: true
});

export function ProcessItem(itemNode, path = [], company, language) {
	CompendiaItemBase.call(this, itemNode, path, company, language);
	this.type = ProcessItem.type;
}

ProcessItem.prototype = Object.create(CompendiaItemBase.prototype);
ProcessItem.prototype.handleCommonNode = function handleCommonNode(node) {
	const tagName = camelCased(node.tagName);

	if (tagName.toLowerCase() === 'processstepitem') {
		this.handleProcessStepItem(node);
	} else {
		CompendiaItemBase.prototype.handleCommonNode.call(this, node);
	}
};

ProcessItem.prototype.handleProcessStepItem = function handleProcessStepItem(node) {
	if (!this.documents) {
		this.documents = [];
	}

	const id = Number(this.getDocumentIdFromProcessStepItem(node));
	if (id) {
		this.documents.push(id);
	}
};

Object.defineProperty(ProcessItem.prototype, 'constructor', {
	enumerable: false,
	value: ProcessItem,
	writable: true
});

CategoryItem.type = 'category';
DocumentItem.type = 'document';
FileItem.type = 'file';
HandbookItem.type = 'handbook';
ProcessItem.type = 'process';
