import sanitizeHtml from 'utils/sanitize-html';
import { deverror } from 'utils/devlog';
import { camelCased } from 'utils/strings';
import { DocumentItem, HandbookItem, ProcessItem } from 'api/compendia-item';
import { SearchResults } from 'api/search-results';

let authTimestamp = 0;
let prevAuthKey = '';
const iframe = document.createElement('iframe');
iframe.height = 0;
iframe.width = 0;
document.body.appendChild(iframe);

function compendiaAuthenticate(handbook, key) {
	const oneHourAgo = Date.now() - (1000 * 60 * 60);

	if (authTimestamp < oneHourAgo) {
		prevAuthKey = '';
	}

	// hacky way to authenticate the user
	if (key !== prevAuthKey) {
		prevAuthKey = key;
		iframe.src = `https://cp.compendia.no/olen-betong/${handbook}/?key=${key}`;
		authTimestamp = Date.now();
	}
}

function CompendiaAPI() {
	this.handbook = {
		documents: {}
	};
	this.isLoaded = false;
	this.loadingDocuments = {};
}

CompendiaAPI.prototype._cleanMarkup = function _cleanMarkup(markup) {
	const div = document.createElement('div');
	div.innerHTML = markup;
	sanitizeHtml(div);

	return div.innerHTML;
};

CompendiaAPI.prototype.baseApiCall = async function baseApiCall({ endpoint, accept = 'xml' }) {
	try {
		const url = CompendiaAPI.baseUrl + endpoint;
		const response = await fetch(
			url,
			{
				credentials: 'include',
				headers: {
					'X-Requested-With': 'XMLHttpRequest'
				}
			}
		);
		
		if (accept === 'xml') {
			const parser = new window.DOMParser();
			const textData = await response.text();

			return parser.parseFromString(textData, 'application/xml');
		} else if (accept === 'json') {
			return await response.json();
		}

		return await response.text();
	} catch (ex) {
		deverror(ex.message);
	}
};

CompendiaAPI.prototype.ensureHandbookIsLoaded = function ensureHandbookIsLoaded() {
	return new Promise(async resolve => {
		if (!this.isLoaded) {
			if (this.__loadPromise instanceof Promise) {
				await this.__loadPromise;
				resolve(true);
			} else {
				setTimeout(async () => {
					await this.ensureHandbookIsLoaded();
					resolve(true);
				}, 250);
			}
		} else {
			resolve(true);
		}
	});
};

CompendiaAPI.prototype.getDocument = async function getDocument(id, path = []) {
	await this.ensureHandbookIsLoaded();

	if (!this.handbook.documents[id]) {
		if (!this.loadingDocuments[id]) {
			this.loadingDocuments[id] = new Promise(async (resolve) => {
				const documentXml = await this.baseApiCall({ endpoint: `${this.company}/${this.language}/document/${id}`});
				const [node] = documentXml.childNodes;
				const tagName = camelCased(node.tagName).toLowerCase();
		
				if (tagName === 'processitem') {
					resolve(new ProcessItem(node, path));
				} else {
					resolve(new DocumentItem(node, path));
				}
			});
		}

		this.handbook.documents[id] = await this.loadingDocuments[id];
		delete this.loadingDocuments[id];
	}
	
	return this.handbook.documents[id];
};

CompendiaAPI.prototype.getMenu = async function getMenu() {
	await this.ensureHandbookIsLoaded();

	return this.handbook.getMenuItem();
};

CompendiaAPI.prototype.getHandbook = async function getHandbook({ company = 'OB', language = 'NO' }) {
	this.isLoaded = false;
	this.company = company;
	this.language = language;

	for (let company of this.userInfo.AvailableCompanies) {
		if (company.Domain === this.company) {
			compendiaAuthenticate(company.HandbookID, company.AuthKey);
			break;
		}
	}

	this.__loadPromise = this.baseApiCall({ endpoint: `${company}/${language}/handbook` });

	const data = await this.__loadPromise;
	
	if (data.childNodes.length > 0) {
		this.handbook = new HandbookItem(data.childNodes[0], [], this.company, this.language);
	} else {
		throw new Error('Handbook API returned invalid document.');
	}

	this.isLoaded = true;
	this.__loadPromise = null;

	return this.handbook;
};

CompendiaAPI.prototype.getUserInfo = async function getUserInfo() {
	this.userInfo = await this.baseApiCall({ endpoint: 'userinfo', accept: 'json' });

	if (this.userInfo.success === false) {
		throw new Error(this.userInfo.statusDescription);
	}

	return this.userInfo;
};

CompendiaAPI.prototype.getSearchResults = async function getSearchResults(query) {
	const data = await this.baseApiCall({ endpoint: `${this.company}/${this.language}/search?query=${encodeURIComponent(query)}` });

	const rss = data.childNodes[0];

	for (let child of rss.childNodes) {
		if (child.nodeType === Node.ELEMENT_NODE) {
			if (child.tagName === 'channel') {
				return new SearchResults(child, this.company);
			}
		}
	}
};

CompendiaAPI.baseUrl = '/api/compendia/v2.0/';

export default CompendiaAPI;
