export function SearchResults(node, company) {
	this.results = [];
	this.company = company;
	this._processResults(node);
}

SearchResults.prototype._processResults = function(node) {
	for (let child of node.childNodes) {
		if (child.nodeType === Node.ELEMENT_NODE) {
			const { tagName } = child;

			if (tagName === 'item') {
				const result = new SearchResult(child, this.company);

				if (result.link) {
					this.results.push(result);
				}
			} else if (tagName.substring(0, 3) === 'os:') {
				this[tagName.substring(3)] = Number(child.textContent);
			} else {
				this[tagName] = child.textContent;
			}
		}
	}
};

export function SearchResult(node, company) {
	this.company = company;
	this.useRouter = false;
	this._processResult(node);
}

SearchResult.prototype._processResult = function(node) {
	for (let child of node.childNodes) {
		if (child.nodeType === Node.ELEMENT_NODE) {
			const { tagName } = child;

			if (tagName.toLowerCase() === 'link' && child.textContent) {
				const url = new URL(child.textContent);
				const localPath = '/olen-betong';

				if (url.pathname.substring(0, localPath.length) === localPath) {
					const parts = url.pathname.split('/');
					url.hostname = location.hostname;

					if (url.pathname.endsWith('download')) {
						let language = parts[2] === 'employee-handbook' ? 'en' : 'no';
						url.hostname = location.hostname;
						url.pathname = `/api/compendia/v2.0/${this.company}/${language}/download/${parts[3]}`;
					} else {
						url.pathname = `/${parts[2]}/${parts[3]}`;
						this.useRouter = true;
					}
				}

				this.link = url.toString();
			} else {
				this[tagName] = child.textContent;
			}
		}
	}
};
