import { handleActions } from 'redux-actions';

export const initialStoreState = {
	categoriesExpanded: {},
	documentPath: [],
	document: {},
	documentError: null,
	documentLoading: false,
	language: 'no',
	menu: [],
	menuError: null,
	menuLoading: false,
	processId: null,
	processes: {},
	processesError: null,
	processesLoading: false,
	userInfo: {},
	userInfoError: null,
	userInfoLoading: false,
	searchError: null,
	searchLoading: false,
	searchResults: [],
	searchQuery: ''
};

const actions = {

	FETCH_DOCUMENT_FAILURE: (state, action) => {
		const { error } = action.payload;

		return {
			...state,
			documentError: error,
			documentLoading: false
		};
	},

	FETCH_DOCUMENT_REQUEST: (state) => ({
		...state,
		documentError: null,
		documentLoading: true
	}),

	FETCH_DOCUMENT_SUCCESS: (state, action) => {
		const { document } = action.payload;

		return {
			...state,
			document,
			documentError: null,
			documentLoading: false
		};
	},

	FETCH_MENU_FAILURE: (state, action) => {
		const { error } = action.payload;

		return {
			...state,
			menuError: error,
			menuLoading: false
		};
	},

	FETCH_MENU_REQUEST: (state) => ({
		...state,
		menuError: null,
		menuLoading: true
	}),

	FETCH_MENU_SUCCESS: (state, action) => {
		const { menu } = action.payload;

		return {
			...state,
			menu,
			menuError: null,
			menuLoading: false
		};
	},

	FETCH_PROCESSES_FAILURE: (state, action) => {
		const { error } = action.payload;

		return {
			...state,
			processesError: error,
			processesLoading: false
		};
	},

	FETCH_PROCESSES_REQUEST: (state) => ({
		...state,
		processesError: null,
		processesLoading: true
	}),

	FETCH_PROCESSES_SUCCESS: (state, action) => {
		const { processes } = action.payload;
		const stateChange = {
			processes,
			processesError: null,
			processesLoading: false
		};

		return {
			...state,
			...stateChange
		};
	},

	FETCH_SEARCH_RESULTS_FAILURE: (state, action) => {
		const { error } = action.payload;

		return {
			...state,
			searchError: error,
			searchLoading: false
		};
	},

	FETCH_SEARCH_RESULTS_REQUEST: state => ({
		...state,
		searchError: null,
		searchLoading: true,
	}),

	FETCH_SEARCH_RESULTS_SUCCESS: (state, action) => {
		const { searchResults } = action.payload;
		const stateChange = {
			searchResults,
			searchError: null,
			searchLoading: false
		};

		return {
			...state,
			...stateChange
		};
	},

	FETCH_USER_INFO_FAILURE: (state, action) => {
		const { error } = action.payload;

		return {
			...state,
			userInfoError: error,
			userInfoLoading: false
		};
	},

	FETCH_USER_INFO_REQUEST: (state) => ({
		...state,
		userInfoError: null,
		userInfoLoading: true
	}),

	FETCH_USER_INFO_SUCCESS: (state, action) => {
		const { userInfo } = action.payload;

		return { ...state, userInfo, userInfoError: null, userInfoLoading: false };
	},

	SET_CATEGORY_EXPANDED: (state, action) => {
		const { expanded, id } = action.payload;
		const categoriesExpanded = Object.assign(
			{},
			state.categoriesExpanded
		);

		categoriesExpanded[id] = expanded;

		return { ...state, categoriesExpanded };
	},

	SET_DOCUMENT_PATH: (state, action) => {
		const { path, processId = null } = action.payload;

		return { ...state, documentPath: path, processId };
	},

	SET_LANGUAGE: (state, action) => {
		const { language } = action.payload;

		return {
			...state,
			language
		};
	},

	SET_SEARCH_QUERY: (state, action) => {
		const { query } = action.payload;

		return { ...state, searchQuery: query };
	},

	RESET_HANDBOOK: (state) => {
		return {
			...state,
			categoriesExpanded: {},
			document: {},
			documentPath: []
		};
	}

};

const handbookReducer = handleActions(actions, initialStoreState);

export default handbookReducer;
