import React, { Suspense } from 'react';
import { Provider } from 'react-redux';
import { Router, Link } from '@reach/router';
import styled from 'styled-components';

import ApiContext from 'api/context';
import CompendiaAPI from 'api/compendia';
import AppBlock from 'components/app-block';
import Handbook from 'components/handbook';
import Spinner from 'components/spinner';
import { initialStoreState } from 'store/reducer';
import { configureStore } from 'store';
import { isDeveloper } from 'utils/appframe';
import { fetchUserInfoRequest, setLanguage, setSearchQuery } from 'store/actions';
import flagGb from 'res/gb.svg';
import flagNo from 'res/no.svg';

const FullSize = styled.div`
	align-items: center;
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 100vh;
	width: 100vw;
`;

const Flag = styled.div`
	height: 6rem;
	margin: 0.5rem;
	min-width: 6rem;
	width: 6rem;
`;

function DevHelper() {
	return <FullSize>
		<Link to='/personalhandbok'>
			<Flag dangerouslySetInnerHTML={{ __html: flagNo }}/>
		</Link>
		<Link to='/employee-handbook'>
			<Flag dangerouslySetInnerHTML={{ __html: flagGb }}/>
		</Link>
	</FullSize>;
}

class App extends React.Component {
	constructor(props) {
		super(props);

		const current = new URL(location.href);
		const query = current.searchParams.get('query');
		const preloadedState = initialStoreState;

		this.api = new CompendiaAPI();
		this.store = configureStore(this.api, [], preloadedState);
		this.setLanguage = this.setLanguage.bind(this);

		if (query) {
			this.store.dispatch(setSearchQuery({ query }));
		}

		if (isDeveloper()) {
			window.HandbookStore = this.store;
			window.CompendiaAPI = this.api;
		}
	}

	componentDidMount() {
		// The API middleware will automatically load the handbook
		// when user info is successfully requested.
		this.store.dispatch(fetchUserInfoRequest());
	}

	setLanguage(language) {
		this.store.dispatch(
			setLanguage({ language })
		);
	}

	render() {
		return <Provider store={this.store}>
			<ApiContext.Provider value={this.api}>
				<Suspense fallback={<AppBlock><Spinner/></AppBlock>}>
					<Router>
						<Handbook path='/employee-handbook/*' lang='en' key='handbook' onSetLanguage={this.setLanguage} />
						<Handbook path='/personalhandbok/*' lang='no' key='handbook' onSetLanguage={this.setLanguage} />
						<DevHelper default/>
					</Router>
				</Suspense>
			</ApiContext.Provider>
		</Provider>;
	}
}

export default App;
