/**
 * HTML content from Compendia tends to be a mess, so we're
 * trying to fix some common problems.
 *  - Hardcoded inline style colors
 *  - Empty paragraphs
 *  - Linebreak at the start or end of paragraphs
 */
function sanitizeHtml(node) {
	let nodeCanBeRemoved = true;
	const illegalColors = [
		'#000000',
		'#800080',
		'rgb(128, 0, 128)',
		'rgb(0, 0, 0)',
		'rgb(255, 0, 0)'
	];

	if (typeof node.parentNode !== 'undefined' && node.childNodes.length > 0) {
		const toRemove = [];

		for (let i = 0; i < node.childNodes.length; i++) {
			const childShouldBeRemoved = sanitizeHtml(node.childNodes[i]);

			if (node.childNodes[i].nodeType === Node.ELEMENT_NODE && childShouldBeRemoved) {
				toRemove.push(node.childNodes[i]);
			}

			if (!childShouldBeRemoved) {
				nodeCanBeRemoved = false;
			}
		}

		for (let j = 0; j < toRemove.length; j++) {
			node.removeChild(toRemove[j]);
		}
	}

	if (node.nodeType === Node.ELEMENT_NODE) {
		const tagName = (node.tagName || '').toUpperCase();

		if (tagName === 'IMG') {
			node.src = node.src.replace(window.location.origin, 'http://cp.compendia.no');
		} else if (tagName === 'A') {
			const compendia = 'http://cp.compendia.no';
			const url = new URL(node.href);
			const localpath = '/olen-betong';

			if (url.hostname === location.hostname && url.pathname.substring(0, localpath.length) === localpath) {
				url.pathname = url.pathname.substring(localpath.length);
				node.href = url.toString();
			} else {
				if (url.hostname === location.hostname) {
					url.hostname = 'cp.compendia.no';
					node.href = url.toString();
				} else if (node.href.substring(0, compendia.length) === compendia) {
					node.href = '/api/compendia/redirect?l=' + node.href;
				}

				node.target = '_blank';
				node.rel = 'noopener noreferrer';
			}

		} else if (['SPAN', 'DIV', 'P', 'FONT', 'TABLE'].includes(tagName)) {
			// We get a lot of nested spans, divs and paragraphs that only contain whitespace, so
			// we check those tags to see if they contain text innerText. If it contains other
			// elements (e.g. img), "nodeCanBeRemoved" will be false.

			if (nodeCanBeRemoved && /^\s*$/.test(node.innerText)) {
				return true;
			}

			if (node.style.color !== '' && illegalColors.includes(node.style.color)) {
				node.style.color = '';
			}

			if (tagName === 'FONT') {
				node.style.color = 'inherit';
				node.style.fontFamily = 'inherit';
				node.style.fontSize = 'inherit';
			}
		} else if (tagName === 'BR') {
			// Removing line breaks that are the first or last elements in a paragraph node
			if (node.parentElement.tagName === 'P') {
				if (node.nextElementSibling === null || node.previousElementSibling === null) {
					return true;
				}
			}
		}
	} else {
		if (/^\s*$/.test(node.textContent)) {
			return true;
		}
	}

	return false;
}

export default sanitizeHtml;
