import React from 'react';
import styled, { keyframes } from 'styled-components';

const bulletSize = 0.5;

const scaleAnimation = keyframes`
	from {
		transform: scale(0);
	}
	to {
		transform: scale(1);
	}
`;

const translateAnimation = keyframes`
	from {
		transform: translate(0, 0);
	}
	to {
		transform: translate(${bulletSize * 1.7}em, 0);
	}
`;

const Wrapper = styled.div`
	display: inline-block;
	height: ${bulletSize * 1.5}em;
	position: relative;
	width: ${bulletSize * 5}em;
`;

const Bullet = styled.div`
	animation-timing-function: cubic-bezier(0, 1, 1, 0);
	background: #666;
	border-radius: 50%;
	height: ${bulletSize}em;
	position: absolute;
	top: 0.2rem;
	width: ${bulletSize}em;

	&:nth-child(1) {
		animation: ${scaleAnimation} 0.6s infinite;
		left: ${bulletSize * 0.5}em;
	}

	&:nth-child(2) {
		animation: ${translateAnimation} 0.6s infinite;
		left: ${bulletSize * 0.5}em;
	}

	&:nth-child(3) {
		animation: ${translateAnimation} 0.6s infinite;
		left: ${bulletSize * 2.4}em;
	}

	&:nth-child(4) {
		animation: ${scaleAnimation} 0.5s infinite reverse;
		left: ${bulletSize * 4}em;
	}
`;

const Spinner = () =>
	<Wrapper>
		<Bullet/>
		<Bullet/>
		<Bullet/>
		<Bullet/>
	</Wrapper>;

export default Spinner;
