/* eslint-disable no-console */
import debounce from 'lodash.debounce';

import * as actions from 'store/actions';
import { pascalCase } from 'utils/strings';

async function performAsyncResourceRequest(api, store, action, resource) {
	const { payload, type } = action;

	if (type.startsWith('FETCH_') && type.endsWith('_REQUEST')) {
		const parts = type.toLowerCase().split('_').slice(1, -1);
		const name = pascalCase(parts);

		if (resource.toLowerCase() === name.toLowerCase()) {
			const apiMethod = `get${name}`;
			const successAction = `fetch${name}Success`;
			const failureAction = `fetch${name}Failure`;

			try {
				const apiRequest = api[apiMethod].bind(api);
				let data = null;

				if (payload && payload.id) {
					data = await apiRequest(payload.id);
				} else if (payload && payload.query) {
					data = await apiRequest(payload.query);
				} else {
					data = await apiRequest();
				}

				store.dispatch(
					actions[successAction]({
						[resource]: data
					})
				);
			} catch (error) {
				console.error(error);

				store.dispatch(
					actions[failureAction]({ error: error.message })
				);
			}
		}
	}
}

async function performSearch(store, query) {
	if (query && query.length > 0) {
		store.dispatch(actions.fetchSearchResultsRequest({ query }));
	} else {
		store.dispatch(actions.fetchSearchResultsSuccess({ searchResults: [] }));
	}
}

const debouncedSearch = debounce(performSearch, 500);

const apiMiddleware = (api) => (store) => (next) => async (action) => {
	next(action);

	performAsyncResourceRequest(api, store, action, 'menu');
	performAsyncResourceRequest(api, store, action, 'document');
	performAsyncResourceRequest(api, store, action, 'processes');
	performAsyncResourceRequest(api, store, action, 'searchResults');
	performAsyncResourceRequest(api, store, action, 'userInfo');

	if (action.type === 'FETCH_USER_INFO_SUCCESS' || action.type === 'SET_LANGUAGE') {
		const state = store.getState();
		const {
			language,
			userInfo: { SelectedCompany }
		} = state;

		if (language && SelectedCompany) {
			api.getHandbook({ language, company: SelectedCompany });

			store.dispatch(actions.resetHandbook());
			store.dispatch(actions.fetchMenuRequest());
		}
	} else if (action.type === 'SET_SEARCH_QUERY') {
		const { query } = action.payload;

		debouncedSearch(store, query);
	} else if (action.type === 'FETCH_SEARCH_RESULTS_SUCCESS') {
		const { searchQuery } = store.getState();
		history.replaceState({}, document.title, '?query=' + encodeURIComponent(searchQuery));
	}
};

export default apiMiddleware;
