import React from 'react';

/**
 * Higher order component (HOC) that tracks window resize events and checks if the size has moved
 * over or below 62rem.
 * @param {*} WrappedComponent Component that should get the minimum breakpoint as a property
 * @return {class} A React component class
 */
function viewportMonitor(WrappedComponent) {
	class WithViewportSize extends React.Component {
		constructor(props) {
			super(props);

			this.handleWindowResized = this.handleWindowResized.bind(this);
			this.isMobileSize = this.isMobileSize.bind(this);
			this._willUnmount = false;
			this.mql = window.matchMedia('(max-width: 62rem)');
			this.state = { isMobileSize: this.isMobileSize() };
		}
		
		componentDidMount() {
			this.mql.addListener(this.handleWindowResized);
		}

		componentWillUnmount() {
			this._willUnmount = true;
			this.mql.removeListener(this.handleWindowResized);
		}

		isMobileSize() {
			return this.mql.matches;
		}

		handleWindowResized() {
			if (!this._willUnmount) {
				this.setState({ isMobileSize: this.isMobileSize() });
			}
		}

		render() {
			return <WrappedComponent
				isMobileSize={this.state.isMobileSize}
				{...this.props}
			/>;
		}
	}

	const displayName = WrappedComponent.displayName || WrappedComponent.name || 'Component';

	WithViewportSize.displayName = `WithViewportSize(${displayName})`;

	return WithViewportSize;
}

export default viewportMonitor;
