export function getArticleId() {
	if (window.af && window.af.article.id) {
		return window.af.article.id;
	}

	return 'personalhandbok';
}

export const getLocale = () => {
	const { af } = window;
	if (af && af.userSession) {
		return af.userSession.uiculture;
	}

	return 'nb-NO';
};

export const localize = (string) => {
	const { af } = window;
	if (af && af.common) {
		return af.common.getLocalizedString(string);
	}

	return string;
};

export const isDeveloper = () => {
	if (location.hostname === 'localhost') {
		return true;
	}

	const { af } = window;

	if (af && af.userSession && af.userSession.isDeveloper) {
		return true;
	}

	return false;
};
