import React, { lazy } from 'react';
import { connect } from 'react-redux';
import { Location } from '@reach/router';
import { Flipper } from 'react-flip-toolkit';

const MobileView = lazy(() => import('components/mobile-view'));
const DesktopView = lazy(() => import('components/desktop-view'));
import viewportMonitor from 'components/viewport-monitor';
import { localize } from 'utils/appframe';

class Handbook extends React.Component {
	componentDidMount() {
		this.props.onSetLanguage(this.props.lang);
	}

	componentDidUpdate(prevProps) {
		if (prevProps.lang !== this.props.lang) {
			this.props.onSetLanguage(this.props.lang);
		}
	}

	render () {
		const { isMobileSize, userInfoError, ...other } = this.props;
		const View = isMobileSize ? MobileView : DesktopView;

		if (userInfoError) {
			return <div className='container' style={{marginTop: '4rem'}}>
				<div className='alert alert-danger'>
					{localize(userInfoError)}
				</div>
			</div>;
		}

		return <Location>
			{({ location }) => <Flipper flipKey={location.key}>
				<View {...other} />
			</Flipper>}
		</Location>;
	}
}

Handbook.defaultProps = {
	lang: 'no'
};

function mapStateToProps(state) {
	const { userInfoError } = state;

	return { userInfoError };
}

export default viewportMonitor(connect(mapStateToProps)(Handbook));
